        <div class="chart-row">
          <div class="chart-container">
            <line-chart v-if="chartData.labels && chartData.datasets.length" :data="chartData" :options="options" @click="onLineChartClick"></line-chart>
          </div>
        </div>
        <div class="final-row">
          <div class="download-btn-container">
            <button class='download-btn' v-if="chartData.labels && chartData.datasets.length" @click="downloadChartData()">Download Data</button>
          </div>
        </div>
