<script>

import { Line } from 'vue-chartjs';
// import { Bar } from 'vue-chartjs';
import { Chart as ChartJS, Title, Tooltip, Legend, LineElement, PointElement, LinearScale, CategoryScale } from 'chart.js'
import zoomPlugin from 'chartjs-plugin-zoom';
ChartJS.register(Title, Tooltip, Legend, LineElement, PointElement, LinearScale, CategoryScale, zoomPlugin)

import WarningDia  from './WarningDia.vue';  
import Multiselect from 'vue-multiselect'
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';


// import { ref } from 'vue';

export default {
  name: 'CodingChallenge',
  props: {},
  components: {
    LineChart: Line,
    // BarChart: Bar,
    WarningDia,
    Multiselect,
    VueDatePicker,
   },
  data(){
    console.log('here')
    return {
      chartData: {},
      statesOptions: ['CO','CA'],
      countryOptions: [{CountryName:'Denver',StateName:'CO'}],
      selectedStates: [],
      selectedCountry: [],
      selectedDateRange: [],
      predictions: [],
      options:{},
      loaded: false,
      isPredicted: false,
      barChartData: null,
  }}, 
  
  methods: {
    toggle() {
        this.isPredicted = !this.isPredicted;
    },
    fetchStates() {
      fetch('http://localhost:5001/getStates',{
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(response => response.json())
        .then(data => {
          this.statesOptions = data; 
        })
        .catch(error => console.error('Error fetching States:', error));
    },
    addStates(newState) {
      this.selectedStates.push(newState)
    },
    addCountries(newCountry){
      const stateAdded = this.countryOptions.some(option => option.countryName === newCountry);
      this.selectedCountry.push({StateName: stateAdded, CountryName: newCountry})
    },
    fetchCountries() {
      if (this.selectedStates.length > 0) {
        fetch('http://localhost:5001/get_countries', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        mode: 'cors',
        body: JSON.stringify({ selectedStates: this.selectedStates })
        })
        .then(response => response.json())
        .then(data => {
          this.countryOptions = data;
        })
        .catch(error => console.error('Error fetching country data:', error))
      }
    },
    fetchPredictions() {
      fetch('http://localhost:5001/predict', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        mode: 'cors',
        body: JSON.stringify({ selectedCountry: this.selectedCountry, selectedDateRange: this.selectedDateRange, isPredicted: this.isPredicted})
        })
        .then(response => response.json())
        .then(data => {
          console.log('predictions fetch', data.predictions)
          this.predictions = data.predictions;
          this.generateChartData(data.predictions);
        })
        .catch(error => console.error('Error fetching country data:', error))
    },
    getPredicted() {
      this.fetchPredictions();
      this.loaded = !this.loaded;

    },
    getRandomColor() {
      const letters = '0123456789ABCDEF';
      let color = '#'; 
      for (let i = 0; i < 6; i++) { 
        color += letters[Math.floor(Math.random() * 16)];
      }
      return color; // Return the generated color
    },
    generateChartData(predictions){
      if (this.selectedCountry.length > 0) {
        const countriesName = this.selectedCountry.map(({ CountryName, StateName, SizeRank }) => {
          return `${CountryName},${StateName},${SizeRank}`
        })
      const groupedData = predictions.map((data, index) => ({
        label: countriesName[index],
        data: data.map(item => item.PredictedPrice),
        borderColor: this.getRandomColor(), 
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
        fill: true
      }))
      const labels = predictions[0].map(item => item.Date);
      this.chartData = {
        labels: labels, 
        datasets: groupedData 
      };
      this.options = {
        responsive: true,
        maintainAspectRatio: 1.5,
        scales: {
              x: {
                grid: {
                  display: true,
                  color: 'rgba(211, 211, 211, 1)',
                  lineWidth: 0.5,
                },
                ticks: {
                  color: 'rgba(211, 211, 211, 1)',
                  font: {
                    size: 14,
                    weight: 'bold'
                  },
                  padding: 10,
                  autoSkip: true,
                  maxRotation: 45,
                  minRotation: 0
                }
              },
              y: {
                grid: {
                  display: true,
                  color: 'rgba(211, 211, 211, 1)',
                  lineWidth: 0.5,
                },
                ticks: {
                  color: 'rgba(211, 211, 211, 1)',
                  font: {
                    size: 14,
                    weight: 'bold'
                  },
                  padding: 10,
                  autoSkip: true,
                  maxRotation: 45,
                  minRotation: 0
                }
              }
        },
        plugins: {
          legend: {
            labels: {
              color: 'black',
            }
          },
          title: {
            display: true,
            text: 'House Price vs Date Graph',
            color: 'black', 
            font: {
              size: '20vmin',
            }
          },
          zoom: {
            zoom: {
              wheel: {
                enabled: true,  // Enable zooming with mouse wheel
                modifierKey: 'ctrl',  // Use the Ctrl key to zoom
              },
              pinch: {
                enabled: true,  // Enable pinch zoom for touch devices
              },
              drag: {
                enabled: true,  // Enable dragging to zoom
              }
            }
         } 
        }
      }
    }else{
        console.error('Empty Countries Selected!');
      }
      
    },
    downloadChartData() {
      const data = this.chartData;  // Get the chart data
      const jsonData = JSON.stringify(data);  // Convert data to JSON
      const blob = new Blob([jsonData], { type: 'application/json' });
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = 'chartData.json';  // Name of the file
      link.click();
    },
    onLineChartClick(event) {
      const activePoint = this.$refs.lineChart.chart.getElementsAtEventForMode(event, 'nearest', { intersect: true }, false);
      if (activePoint.length > 0) {
        const monthIndex = activePoint[0].index;
        const selectedMonth = this.lineChartData.labels[monthIndex];
        
        // Fetch and update bar chart data based on the selected month
        this.updateBarChart(selectedMonth);
      }
    },
    updateBarChart(selectedMonth) {
      console.log('barchart',this.chartData.dataset,selectedMonth);
      // const barData = {
      //   labels: this.selectedCountry,
      //   datasets: [{
      //     label: 'House Price',
      //     data: selectedCountries.map(country => countryPrices[country][selectedMonth]),
      //     backgroundColor: 'rgba(75, 192, 192, 0.5)'
      //   }]
      // };
    },
    summary(){
      if (!this.chartData.datasets.length) {
        return [];
      }
      this.summaryArray = this.chartData.datasets.map((dataset) => {
        if (Array.isArray(dataset.data) && dataset.data.length) {
          return {
            label:dataset.label,
            maxProfit: Math.max(...dataset.data),
            maxLoss: Math.min(...dataset.data),
            breakEvenPoint: dataset.breakEvenPoint,
          };
        }
      });
      
    },
    showInstruction(){
      this.showInstructionDialog = true;
    },
  },
  mounted() {
    this.fetchStates();  // Fetch countries when the component is mounted
  },
  watch: {
    selectedStates() {
      console.log('states')
      this.countryOptions = this.fetchCountries();
    },
  },
  
}
</script>
